import React, { useEffect, useState, useRef } from 'react';
import { Card, Select, Row, Col, Typography, Button, Alert, Space, Statistic } from 'antd';
import { LineChartOutlined, ReloadOutlined, BarChartOutlined } from '@ant-design/icons';
import * as echarts from 'echarts';
import { monitoringService } from '../../services/monitoringService';
import type { DataFlowMetrics } from '../../types/monitoring';

// Time range options constant
const TIME_RANGE_OPTIONS = [
  { value: '1h', label: 'è¿‡å»1å°æ—¶' },
  { value: '6h', label: 'è¿‡å»6å°æ—¶' },
  { value: '12h', label: 'è¿‡å»12å°æ—¶' },
  { value: '24h', label: 'è¿‡å»24å°æ—¶' },
  { value: '7d', label: 'è¿‡å»7å¤©' },
];

const { Option } = Select;
const { Text } = Typography;

interface DataFlowChartProps {
  height?: number;
  autoRefresh?: boolean;
  refreshInterval?: number;
}

interface ChartData {
  time: string[];
  throughput: number[];
  latency: number[];
  errorRate: number[];
  devices: { name: string; value: number }[];
}

const DataFlowChart: React.FC<DataFlowChartProps> = ({
  height = 400,
  autoRefresh = true,
  refreshInterval = 10000,
}) => {
  // çŠ¶æ€ç®¡ç†
  const [chartData, setChartData] = useState<ChartData>({
    time: [],
    throughput: [],
    latency: [],
    errorRate: [],
    devices: []
  });
  const [loading, setLoading] = useState(false);
  const [timeRange, setTimeRange] = useState('1h');
  const [chartType, setChartType] = useState<'line' | 'bar'>('line');
  const [, setError] = useState<string | null>(null);

  // Chartå®ä¾‹å¼•ç”¨
  const throughputChartRef = useRef<HTMLDivElement>(null);
  const latencyChartRef = useRef<HTMLDivElement>(null);
  const errorChartRef = useRef<HTMLDivElement>(null);
  const deviceChartRef = useRef<HTMLDivElement>(null);
  
  const throughputChart = useRef<echarts.ECharts | null>(null);
  const latencyChart = useRef<echarts.ECharts | null>(null);
  const errorChart = useRef<echarts.ECharts | null>(null);
  const deviceChart = useRef<echarts.ECharts | null>(null);

  // æ•°æ®åŠ è½½
  const loadData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const endTime = new Date();
      const startTime = new Date();
      
      // æ ¹æ®æ—¶é—´èŒƒå›´è°ƒæ•´å¼€å§‹æ—¶é—´
      switch (timeRange) {
        case '1h':
          startTime.setHours(endTime.getHours() - 1);
          break;
        case '6h':
          startTime.setHours(endTime.getHours() - 6);
          break;
        case '12h':
          startTime.setHours(endTime.getHours() - 12);
          break;
        case '24h':
          startTime.setDate(endTime.getDate() - 1);
          break;
        case '7d':
          startTime.setDate(endTime.getDate() - 7);
          break;
      }

      // æ¨¡æ‹Ÿæ•°æ®åŠ è½½ï¼ˆå®é™…åº”è¯¥è°ƒç”¨APIï¼‰
      const metrics = await monitoringService.getDataFlowMetrics({
        time_range: timeRange,
        limit: 100
      });
      
      const processedData = processChartData(metrics.metrics || []);
      setChartData(processedData);
      
    } catch (err: unknown) {
      console.error('è·å–æ•°æ®æµæŒ‡æ ‡å¤±è´¥:', err);
      setError(err instanceof Error ? err.message : 'æœªçŸ¥é”™è¯¯');
    } finally {
      setLoading(false);
    }
  };

  // å¤„ç†å›¾è¡¨æ•°æ®
  const processChartData = (metrics: DataFlowMetrics[]) => {
    const now = new Date();
    const timePoints = [];
    const throughputData = [];
    const latencyData = [];
    const errorRateData = [];
    
    // ç”Ÿæˆæ—¶é—´ç‚¹
    for (let i = 23; i >= 0; i--) {
      const time = new Date(now.getTime() - i * 300000); // æ¯5åˆ†é’Ÿä¸€ä¸ªç‚¹
      timePoints.push(time.toISOString());
      
      // æ¨¡æ‹Ÿæ•°æ®ï¼ˆå®é™…åº”è¯¥æ ¹æ®metricsè®¡ç®—ï¼‰
      throughputData.push(Math.floor(Math.random() * 100) + 50);
      latencyData.push(Math.floor(Math.random() * 50) + 10);
      errorRateData.push(Math.random() * 5);
    }
    
    // å¤„ç†è®¾å¤‡åˆ†å¸ƒæ•°æ®
    const deviceMap = new Map<string, number>();
    metrics.forEach(metric => {
      const count = deviceMap.get(metric.device_id) || 0;
      deviceMap.set(metric.device_id, count + 1);
    });
    
    const devices = Array.from(deviceMap.entries()).map(([name, value]) => ({ name, value }));
    
    return {
      time: timePoints,
      throughput: throughputData,
      latency: latencyData,
      errorRate: errorRateData,
      devices
    };
  };

  // åˆå§‹åŒ–å›¾è¡¨
  const initCharts = () => {
    if (throughputChartRef.current) {
      throughputChart.current = echarts.init(throughputChartRef.current);
    }
    if (latencyChartRef.current) {
      latencyChart.current = echarts.init(latencyChartRef.current);
    }
    if (errorChartRef.current) {
      errorChart.current = echarts.init(errorChartRef.current);
    }
    if (deviceChartRef.current) {
      deviceChart.current = echarts.init(deviceChartRef.current);
    }
  };

  // æ›´æ–°å›¾è¡¨
  const updateCharts = () => {
    // ååé‡å›¾è¡¨
    if (throughputChart.current) {
      const option = {
        title: {
          text: 'æ•°æ®ååé‡',
          left: 'center',
          textStyle: { fontSize: 14 },
        },
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>ååé‡: {c} ç‚¹/ç§’',
        },
        xAxis: {
          type: 'category',
          data: chartData.time,
          axisLabel: { fontSize: 10 },
        },
        yAxis: {
          type: 'value',
          name: 'ç‚¹/ç§’',
          axisLabel: { fontSize: 10 },
        },
        series: [{
          data: chartData.throughput,
          type: chartType,
          smooth: true,
          areaStyle: chartType === 'line' ? { opacity: 0.3 } : undefined,
          itemStyle: { color: '#1890ff' },
        }],
        grid: {
          left: 60,
          right: 20,
          top: 50,
          bottom: 40,
        },
      };
      throughputChart.current.setOption(option);
    }

    // å»¶è¿Ÿå›¾è¡¨
    if (latencyChart.current) {
      const option = {
        title: {
          text: 'ç½‘ç»œå»¶è¿Ÿ',
          left: 'center',
          textStyle: { fontSize: 14 },
        },
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>å»¶è¿Ÿ: {c} ms',
        },
        xAxis: {
          type: 'category',
          data: chartData.time,
          axisLabel: { fontSize: 10 },
        },
        yAxis: {
          type: 'value',
          name: 'æ¯«ç§’',
          axisLabel: { fontSize: 10 },
        },
        series: [{
          data: chartData.latency,
          type: chartType,
          smooth: true,
          areaStyle: chartType === 'line' ? { opacity: 0.3 } : undefined,
          itemStyle: { color: '#52c41a' },
        }],
        grid: {
          left: 60,
          right: 20,
          top: 50,
          bottom: 40,
        },
      };
      latencyChart.current.setOption(option);
    }

    // é”™è¯¯ç‡å›¾è¡¨
    if (errorChart.current) {
      const option = {
        title: {
          text: 'é”™è¯¯ç‡',
          left: 'center',
          textStyle: { fontSize: 14 },
        },
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>é”™è¯¯ç‡: {c}%',
        },
        xAxis: {
          type: 'category',
          data: chartData.time,
          axisLabel: { fontSize: 10 },
        },
        yAxis: {
          type: 'value',
          name: '%',
          max: 100,
          axisLabel: { fontSize: 10 },
        },
        series: [{
          data: chartData.errorRate.map(rate => (rate * 100).toFixed(2)),
          type: chartType,
          smooth: true,
          areaStyle: chartType === 'line' ? { opacity: 0.3 } : undefined,
          itemStyle: { color: '#f5222d' },
        }],
        grid: {
          left: 60,
          right: 20,
          top: 50,
          bottom: 40,
        },
      };
      errorChart.current.setOption(option);
    }

    // è®¾å¤‡åˆ†å¸ƒå›¾è¡¨ï¼ˆé¥¼å›¾ï¼‰
    if (deviceChart.current) {
      const option = {
        title: {
          text: 'è®¾å¤‡æ•°æ®åˆ†å¸ƒ',
          left: 'center',
          textStyle: { fontSize: 14 },
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a}<br/>{b}: {c} ({d}%)',
        },
        legend: {
          bottom: 10,
          left: 'center',
          textStyle: { fontSize: 10 },
        },
        series: [{
          name: 'è®¾å¤‡æ•°æ®é‡',
          type: 'pie',
          radius: ['40%', '70%'],
          center: ['50%', '45%'],
          avoidLabelOverlap: false,
          label: {
            show: false,
          },
          emphasis: {
            label: {
              show: true,
              fontSize: 12,
              fontWeight: 'bold',
            },
          },
          data: chartData.devices,
        }],
      };
      deviceChart.current.setOption(option);
    }
  };

  // çª—å£å¤§å°å˜åŒ–æ—¶è°ƒæ•´å›¾è¡¨
  const handleResize = () => {
    throughputChart.current?.resize();
    latencyChart.current?.resize();
    errorChart.current?.resize();
    deviceChart.current?.resize();
  };

  // ç»„ä»¶æŒ‚è½½å’Œå¸è½½
  useEffect(() => {
    initCharts();
    loadData();

    window.addEventListener('resize', handleResize);
    
    return () => {
      window.removeEventListener('resize', handleResize);
      throughputChart.current?.dispose();
      latencyChart.current?.dispose();
      errorChart.current?.dispose();
      deviceChart.current?.dispose();
    };
  }, []);

  // æ•°æ®å˜åŒ–æ—¶æ›´æ–°å›¾è¡¨
  useEffect(() => {
    updateCharts();
  }, [chartData, chartType]);

  // æ—¶é—´èŒƒå›´å˜åŒ–æ—¶é‡æ–°åŠ è½½æ•°æ®
  useEffect(() => {
    loadData();
  }, [timeRange]);

  // è‡ªåŠ¨åˆ·æ–°
  useEffect(() => {
    if (!autoRefresh) return;

    const interval = setInterval(loadData, refreshInterval);
    return () => clearInterval(interval);
  }, [autoRefresh, refreshInterval, timeRange]);

  // è®¡ç®—æ€»ä½“æŒ‡æ ‡
  const totalThroughput = chartData.throughput.reduce((sum, value) => sum + value, 0);
  const avgLatency = chartData.latency.reduce((sum, value) => sum + value, 0) / chartData.latency.length;
  const avgErrorRate = chartData.errorRate.reduce((sum, value) => sum + value, 0) / chartData.errorRate.length;
  const activeDevices = chartData.devices.length;

  return (
    <div>
      {/* æ§åˆ¶é¢æ¿ */}
      <Card size="small" style={{ marginBottom: 16 }}>
        <Row gutter={16} align="middle">
          <Col>
            <Space>
              <Text>æ—¶é—´èŒƒå›´:</Text>
              <Select value={timeRange} onChange={setTimeRange} style={{ width: 120 }}>
                {TIME_RANGE_OPTIONS.map(option => (
                  <Option key={option.value} value={option.value}>
                    {option.label}
                  </Option>
                ))}
              </Select>
            </Space>
          </Col>
          <Col>
            <Space>
              <Text>å›¾è¡¨ç±»å‹:</Text>
              <Select value={chartType} onChange={setChartType} style={{ width: 100 }}>
                <Option value="line">
                  <LineChartOutlined /> æŠ˜çº¿å›¾
                </Option>
                <Option value="bar">
                  <BarChartOutlined /> æŸ±çŠ¶å›¾
                </Option>
              </Select>
            </Space>
          </Col>
          <Col>
            <Button icon={<ReloadOutlined />} onClick={loadData} loading={loading}>
              åˆ·æ–°
            </Button>
          </Col>
        </Row>
      </Card>

      {/* ç»Ÿè®¡ä¿¡æ¯ */}
      <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
        <Col xs={24} sm={6}>
          <Card size="small">
            <Statistic
              title="æ€»ååé‡"
              value={totalThroughput.toFixed(1)}
              suffix="ç‚¹/ç§’"
              valueStyle={{ color: '#1890ff', fontSize: '18px' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={6}>
          <Card size="small">
            <Statistic
              title="å¹³å‡å»¶è¿Ÿ"
              value={avgLatency.toFixed(1)}
              suffix="ms"
              valueStyle={{ color: '#52c41a', fontSize: '18px' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={6}>
          <Card size="small">
            <Statistic
              title="é”™è¯¯ç‡"
              value={(avgErrorRate * 100).toFixed(2)}
              suffix="%"
              valueStyle={{ 
                color: avgErrorRate > 0.05 ? '#f5222d' : '#52c41a',
                fontSize: '18px' 
              }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={6}>
          <Card size="small">
            <Statistic
              title="æ´»è·ƒè®¾å¤‡"
              value={activeDevices}
              suffix="ä¸ª"
              valueStyle={{ color: '#722ed1', fontSize: '18px' }}
            />
          </Card>
        </Col>
      </Row>

      {/* æ•°æ®æµå›¾è¡¨ */}
      <Row gutter={[16, 16]}>
        <Col xs={24} lg={12}>
          <Card size="small">
            <div ref={throughputChartRef} style={{ height: height / 2 }} />
          </Card>
        </Col>
        <Col xs={24} lg={12}>
          <Card size="small">
            <div ref={latencyChartRef} style={{ height: height / 2 }} />
          </Card>
        </Col>
        <Col xs={24} lg={12}>
          <Card size="small">
            <div ref={errorChartRef} style={{ height: height / 2 }} />
          </Card>
        </Col>
        <Col xs={24} lg={12}>
          <Card size="small">
            <div ref={deviceChartRef} style={{ height: height / 2 }} />
          </Card>
        </Col>
      </Row>

      {/* å½“æ²¡æœ‰æ•°æ®æ—¶æ˜¾ç¤ºæç¤º */}
      {chartData.devices.length === 0 && !loading && (
        <Alert
          message="æš‚æ— æ•°æ®æµæ•°æ®"
          description="å½“å‰æ²¡æœ‰æ£€æµ‹åˆ°æ•°æ®æµæ´»åŠ¨ï¼Œè¯·æ£€æŸ¥é€‚é…å™¨æ˜¯å¦æ­£åœ¨è¿è¡Œå¹¶äº§ç”Ÿæ•°æ®ã€‚"
          type="info"
          showIcon
          style={{ marginTop: 16 }}
        />
      )}
    </div>
  );
};

export default DataFlowChart;